/* ---------------------- */
*{
  margin: 0;
  border: 0;
  box-sizing: border-box;
}
.form{
  display: flex;
  flex-direction: rows;
  justify-content: center;
  align-items: center;
  gap: 40px;
}
.box{
  /* display: grid; */
  grid-template-columns: auto auto auto;
  gap: 10px;
  margin-top: 1.3rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  height: 150px;
  width: 200px;
  border: 1px solid black;
  position: relative;
}